<template>
  <UserInfo @mb_name_change="mb_name_change" @mb-id="set_mbid" @is-login="emitisLogin" @set-chat-id="setChatId" :isLogin="isLogin" :mb_id="mb_id" :mb_pw="mb_pw" :userData="userData"/>
  <Timer @is-timeover="timeover" :testTime="testTime" v-if="isStart"/>
  <Main @start-test="doStart" @check-correct="checkCorrect" @score-str="resultMsg" @set-start-date="setStartDate" v-if="isLogin"/>
  <ResultTable v-if="isResult" :scoring="scoring"/>
</template>

<script>
import userJson from './assets/data/user.json';
import axios from 'axios';
import UserInfo from "@/components/UserInfo";
import Main from "@/components/Main";
import ResultTable from "@/components/ResultTable";
import Timer from "@/components/Timer";

export default {
  name: 'App',
  watch: {
    scoreStr(v) {
      console.log(v);
    },
    isStart(v) {
      console.log('isStart : ' + v);
    },
    testTime(v) {
      console.log('testTime : ' + v);
    }
  },
  data() {
    return {
      isLogin: false,
      isStart: false,
      isResult: false,
      isAllCorrect: false,
      testTime: 0,
      step: 0,
      mb_id: '',
      mb_pw: '',
      mb_name: '',
      userData: userJson,
      telegramIds: [],
      qA: 0,
      qB: 0,
      answer: Number,
      answer1: Number,
      page: 0,
      scoreStr: '',
      scoring: []
    }
  },
  methods: {
    emitisLogin(val) {
      this.isLogin = val;
    },
    mb_name_change(val) {
      this.mb_name = val;
    },
    set_mbid(val) {
      this.mb_id = val;
    },
    setChatId(val) {
      console.log(val);
      this.telegramIds = val;
    },
    checkCorrect(val) {
      this.isAllCorrect = val;
    },
    setStartDate(val) {
      this.startDate = val;
    },
    resultMsg(val) {
      this.scoreStr = val;
    },
    doStart(val) {
      console.log(val);
      let symbol = val.symbol;

      if (this.mb_id === 'test') {
        if (symbol === '÷' || symbol === 'X') {
          this.testTime = 900;
        } else {
          this.testTime = 420;
        }
      }
      else if (this.mb_id === 'charlotte') {
        if (symbol === '÷' || symbol === 'X') {
          this.testTime = 900;
        } else {
          this.testTime = 420;
        }
      }
      else if (this.mb_id === 'sei') {
        if (symbol === '÷' || symbol === 'X') {
          this.testTime = 900;
        } else {
          this.testTime = 600;
        }
      }
      this.isStart = val.isStart;
    },
    timeover(val) {
      if (val) {
        Object.assign(this.$data, this.$options.data());
        location.reload();
      }
    }
    ,
    doSubmit() {
      this.endDate = new Date();

      if (!this.isAllCorrect) {
        alert('오답이 존재하므로 전송할 수 없습니다.');
        return;
      }

      // let sendText = this.mb_name + ' [' + this.getToday() + '] ' + symbolStr + '(' + digitStr + ') 완료 : ' + this.scoreStr + ' - 시간 : ' + this.elapsedTime(this.endDate - this.startDate);
      let sendText = this.mb_name + ' [' + this.getToday() + '] ' + this.scoreStr + ' - 시간 : ' + this.elapsedTime(this.endDate - this.startDate);

      console.log(sendText);

      console.log(this.telegramIds);

      let retVal = confirm('결과를 전송하시겠습니까?');

      if (retVal) {
        axios.post('/api/sendMessage', {
          chatId: this.telegramIds,
          msg: sendText
        }).then((response) => {
          this.isResult = false;
          Object.assign(this.$data, this.$options.data());
          location.reload();
          // this.refreshAll();
          alert('메세지 전송 완료!!');
          console.log(response);
        }).catch((error) => {
          alert('메세지 전송 중 오류가 발생했습니다.\n' + error);
        });
      }
      //   for (var i = 0; i < this.telegramIds.length; i++) {
      //     var chatId = this.telegramIds[i];
      //     axios.get(`https://api.telegram.org/bot1115153073:AAHuQ0C5Ad0oM0N6n7yTwwSk4H2lBrujnxA/sendMessage?chat_id=${chatId}&text=${encodeURI(sendText)}`
      //     ).then((response) => {
      //       console.log(response);
      //     }).catch((error) => {
      //       alert('메세지 전송 중 오류가 발생했습니다.\n' + error);
      //     });
      //   }
      //   this.isResult = false;
      //   alert('메세지 전송 완료!!');
      //   this.refreshAll();
    },
    showResult(scores, isResult) {
      this.scoring = scores;
      this.isResult = isResult;
    },
    setSelected(cmd, value) {
      if (cmd === 'symbol') {
        this.selSymbol = value;
      } else if (cmd === 'digit') {
        this.selDigit = value;
      } else if (cmd === 'examcount') {
        this.examCount = value;
      }
      this.onChange(cmd);
    },
    elapsedTime(diff) {
      var minutes = Math.floor(diff / 60000);
      var seconds = ((diff % 60000) / 1000).toFixed(0);

      return minutes + "분" + (seconds < 10 ? '0' : '') + seconds + '초';
      // const times = [
      //   {time: "초", milliSeconds: 1000},
      //   {time: "분", milliSeconds: 1000 * 60},
      //   {time: "시간", milliSeconds: 1000 * 60 * 60},
      //   {time: "일", milliSeconds: 1000 * 60 * 60 * 24},
      //   {time: "개월", milliSeconds: 1000 * 60 * 60 * 24 * 30},
      //   {time: "년", milliSeconds: 1000 * 60 * 60 * 24 * 365},
      // ].reverse();
      //
      // // 년 단위부터 알맞는 단위 찾기
      // for (const value of times) {
      //   const betweenTime = Math.floor(diff / value.milliSeconds);
      //
      //   // 큰 단위는 0보다 작은 소수 단위 나옴
      //   if (betweenTime > 0) {
      //     return `${betweenTime}${value.time}`;
      //   }
      // }
      // return '이상하다';
    },
    getToday(){
      var date = new Date();
      var year = date.getFullYear();
      var month = ("0" + (1 + date.getMonth())).slice(-2);
      var day = ("0" + date.getDate()).slice(-2);
      var hours = ("0" + date.getHours()).slice(-2);
      var minutes = ("0" + date.getMinutes()).slice(-2);
      var seconds = ("0" + date.getSeconds()).slice(-2);

      return year + "-" + month + "-" + day + " " + hours + ":" + minutes + ":" + seconds;
    },
  },
  components: {
    Timer,
    ResultTable,
    Main,
    UserInfo
  }
}
</script>

