<template>
  <div class="container text-center" v-if="isStart">
    <div class="row">
      <div class="col-3">{{ page + 1 }}번 :</div>
      <div class="col">{{ qA }}</div>
      <div class="col">{{ selSymbol === '구구단' ? 'X' : selSymbol }}</div>
      <div class="col">{{ qB }}</div>
      <div class="col">=</div>
      <div class="col" v-if="selSymbol === '÷'">
        <input type="number" ref="answerBox" v-model.number="answer" @keyup.left="drawQuestion('pre')" @keyup.right="drawQuestion('next')" @keyup.enter="drawQuestion('next')" style="width: 80px">
      </div>
      <div class="col" v-if="selSymbol === '÷'">
        ...
      </div>
      <div class="col" v-if="selSymbol === '÷'">
        <input type="number" ref="answerBox" v-model.number="answer1" @keyup.left="drawQuestion('pre')" @keyup.right="drawQuestion('next')" @keyup.enter="drawQuestion('next')" style="width: 50px" autofocus>
      </div>
      <div class="col" v-if="selSymbol !== '÷'">
        <input type="number" ref="answerBox" v-model.number="answer" @keyup.left="drawQuestion('pre')" @keyup.right="drawQuestion('next')" @keyup.enter="drawQuestion('next')" style="width: 120px">
      </div>
    </div>
  </div>

  <div class="header" v-if="isStart">
    <div class="header-button-left">
      <button class="btn btn-outline-primary" @click="drawQuestion('pre')" v-if="page > 0">이전문제</button>
    </div>
    <div class="header-button-center">
      <button class="btn btn-outline-primary" @click="showResult">결과보기</button>
    </div>
    <div class="header-button-right">
      <button class="btn btn-outline-primary" @click="drawQuestion('next')" v-if="page < examCount - 1">다음문제</button>
    </div>
  </div>

  <div class="container text-center" style="width: 100vw" v-if="isStart === false">
    <div class="row">
      <div class="col">
        <p>연산</p>
      </div>
      <div class="col">
        <select v-model="selSymbol" @change="onChange('symbol')" class="make-select">
          <option v-for="(symbol, i) in symbols" :key="symbol" :value="symbol" :selected="i === 0">{{ symbol }}</option>
        </select>
      </div>
    </div>
    <div class="row">
      <div class="col">
        <p>자리수</p>
      </div>
      <div class="col">
        <select v-model="selDigit" @change="onChange('digit')" class="make-select">
          <option v-for="(digit, i) in digits" :key="digit" :value="digit" :selected="i === 0">{{ digit }}</option>
        </select>
      </div>
    </div>
    <div class="row">
      <div class="col">
        <p>문항수</p>
      </div>
      <div class="col">
        <select v-model.number="examCount" class="make-select">
          <option v-for="(cnt, i) in examCounts" :key="cnt" :selected="i === 0">{{ cnt }}</option>
        </select>
      </div>
    </div>
    <div class="row">
      <div class="col">
        <button class="btn btn-outline-success" @click="doStart()">시작</button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Main",
  emits: ['startTest', 'checkCorrect', 'scoreStr', 'setStartDate'],
  data() {
    return {
      isStart: false,   //시작 여부
      symbols: ['+', '-', 'X', '÷', '구구단'],   //연산 리스트
      selSymbol : 'X',    //선택된 연산
      digits: ['1x1', '2x1', '2x2'],    //자리수
      selDigit: '2x1',    //선택된 자리수
      examCounts: Array.from({length: 30}, (v ,i) => i + 1).reverse(),  //문항수
      examCount: 10,    //선택된 문항수
      answers: [],    //입력된 답
      answers1: [],   //입력된 나머지
      scoring: [],    //정답 목록
      qA: 0,          //현재 문제 A
      qB: 0,          //현재 문제 B
      answer: Number,
      answer1: Number,
      testA: [],
      testB: [],
      page: 0,
      startDate: '',
      endDate: '',
    }
  },
  props: {
    isLogin: Boolean,
    isResult: Boolean,
    checkCorrect: Boolean,
    userData: Array,
    telegramIds: Array,
  },
  methods: {
    onChange(cmd) {
      console.log(cmd);
      console.log(this.selSymbol);
      if (cmd === 'symbol') {
        this.digits = [];

        this.examCounts = Array.from({length: 30}, (v, i) => i + 1).reverse();

        if (this.selSymbol === '구구단') {
          this.digits.push('MIX');
          for (var i = 9; i > 1; i--) {
            this.digits.push(i);
          }
          this.selDigit = 'MIX';
          this.examCount = 30;
        } else if (this.selSymbol === 'X') {
          this.digits.push('1x1');
          this.digits.push('2x1');
          this.digits.push('2x2');

          this.selDigit = '2x1';
          this.examCount = 10;
        } else {
          this.digits.push('한자리');
          this.digits.push('두자리');
          this.digits.push('세자리');

          this.selDigit = '두자리';
          this.examCount = 10;
        }
      }
      console.log(this.digits);
    },
    doStart() {
      console.log(this.selSymbol);

      for (var i = 0; i < this.examCount; i++) {
        this.scoring[i] = -1;
        this.answers[i] = 0;
        this.answers1[i] = 0;
      }

      this.isStart = true;
      this.startDate = new Date();

      if (this.selSymbol === '+') {
        this.makePlus();
      } else if (this.selSymbol === '-') {
        this.makeMinus();
      } else if (this.selSymbol === 'X') {
        this.makeMulti();
      } else if (this.selSymbol === '÷') {
        this.makeDevide();
      } else if (this.selSymbol === '구구단') {
        this.makeMultiTable();
      }
      console.log(this.testA);
      console.log(this.testB);
      this.page = 0;
      this.$emit('startTest', {'isStart': true, 'symbol': this.selSymbol});
    },
    drawQuestion(cmd) {
      console.log(this.page, this.examCount, this.selSymbol, (this.selSymbol==='÷'));
      if (cmd === 'pre' && (this.page <= 0)) return;
      if (cmd === 'next' && (this.page >= this.examCount - 1)) return;

      this.answers[this.page] = this.answer;

      if (this.selSymbol === '÷') {
        this.answers1[this.page] = this.answer1;
      }

      if (this.selSymbol === 'X' || this.selSymbol === '구구단') {
        this.scoring[this.page] = (this.qA * this.qB === this.answer) ? 1 : 0;
      } else if (this.selSymbol === '÷') {
        this.scoring[this.page] = (parseInt(this.qA / this.qB) === this.answer) ? ((this.qA % this.qB === this.answer1) ? 1 : 0) : 0;
      } else if (this.selSymbol === '+') {
        this.scoring[this.page] = (this.qA + this.qB === this.answer) ? 1 : 0;
      } else if (this.selSymbol === '-') {
        this.scoring[this.page] = (this.qA - this.qB === this.answer) ? 1 : 0;
      }

      console.log(this.answers);
      console.log(this.scoring);

      if (cmd === 'pre' && this.page > 0) {
        this.page--;
      } else if (cmd === 'next' && this.page < 30) {
        this.page++;
      }

      this.qA = this.testA[this.page];
      this.qB = this.testB[this.page];

      console.log('This page :', this.page);
      console.log('This score :', this.scoring[this.page]);
      console.log(this.scoring[this.page] >= 0);

      if (this.scoring[this.page] >= 0) {
        console.log('Score True');
        this.answer = this.answers[this.page];

        if (this.selSymbol === '÷') {
          this.answer1 = this.answers1[this.page];
        }
        // this.$refs['answerBox'].value = this.answers[this.page];
      } else {
        console.log('Score False');
        this.answer = '';

        if (this.selSymbol === '÷') {
          this.answer1 = '';
        }
        // this.$refs['answerBox'].value = '';
      }
      this.$refs['answerBox'].focus();
      console.log(this.scoring.target);
    },
    makeMulti() {
      let snsub = this.selDigit.slice(0, 1);
      let ensub = this.selDigit.slice(2, 3);

      console.log(snsub + "x" + ensub);

      let sa = (snsub === '1' ? 3 : 30);
      let ea = (snsub === '1' ? 9 : 99);

      let sb = (ensub === '1' ? 2 : 20);
      let eb = (ensub === '1' ? 9 : 99);

      for ( let i = 0; i < this.examCount; i++) {
        let a = Math.floor((Math.random() * (ea - sa)) + sa);
        let b = Math.floor((Math.random() * (eb - sb)) + sb);

        if (this.selDigit !== '1x1') {
          /* eslint-disable */
          while (true) {
            if (this.testA.indexOf(a) > 0) {
              a = Math.floor((Math.random() * (ea - sa)) + sa);
            } else if (a.toString().charAt(a.toString().length - 1) == '0') {
              a = Math.floor((Math.random() * (ea - sa)) + sa);
            } else {
              break;
            }
          }
        }

        this.testA.push(a);
        this.testB.push(b);
      }
      this.qA = this.testA[0];
      this.qB = this.testB[0];
    },
    makeDevide() {
      let sn = 11;
      let en = 99;

      for ( let i = 0; i < this.examCount; i++) {
        let a = Math.floor((Math.random() * (en - sn)) + sn);
        let b = Math.floor((Math.random() * (9 - 2)) + 2);

        /* eslint-disable */
        while (true) {
          if (this.testA.indexOf(a) > 0) {
            a = Math.floor((Math.random() * (en - sn)) + sn);
            // } else if (a.toString().charAt(0) >= b) {
            //   a = Math.floor((Math.random() * (en - sn)) + sn);
          } else {
            break;
          }
        }

        this.testA.push(a);
        this.testB.push(b);
      }
      this.qA = this.testA[0];
      this.qB = this.testB[0];
    },
    makeMultiTable() {
      var continueNum = 1

      if (this.examCount > 16)
        continueNum = Math.trunc(this.examCount / 10) + 1;
      else if (this.examCount > 8)
        continueNum = 2

      console.log(continueNum);

      for ( let i = 0; i < this.examCount; i++) {
        let a = Math.floor(Math.random() * (9 - 2 + 1)) + 2;
        let b = Math.floor(Math.random() * (9 - 2 + 1)) + 2;

        while (this.testB.filter(element => b === element).length >= continueNum) {
          b = Math.floor(Math.random() * (9 - 2 + 1)) + 2;
        }

        this.testA.push(a);
        this.testB.push(b);

      }

      this.shuffle(this.testB);
      console.log('a', this.testA);
      console.log('b', this.testB);

      this.qA = this.testA[0];
      this.qB = this.testB[0];
    },
    makePlus() {
      let sn = (this.selDigit === '한자리' ? 3 : (this.selDigit === '두자리' ? 30 : 300 ));
      let en = (this.selDigit === '한자리' ? 9 : (this.selDigit === '두자리' ? 99 : 999 ));

      for ( let i = 0; i < this.examCount; i++) {
        let a = Math.floor((Math.random() * (en - sn)) + sn);
        let b = Math.floor((Math.random() * (en - sn)) + sn);

        if (this.selDigit !== '한자리') {
          /* eslint-disable */
          while (true) {
            if (this.testA.indexOf(a) > 0) {
              a = Math.floor((Math.random() * (en - sn)) + sn);
              console.log('a : ', a);
            } else  if (this.testB.indexOf(b) > 0) {
              b = Math.floor((Math.random() * (en - sn)) + sn);
              console.log('a : ', b);
            } else  if (a === b) {
              b = Math.floor((Math.random() * (en - sn)) + sn);
              console.log('ab : ', b);
            } else {
              break;
            }
          }
        }

        this.testA.push(a);
        this.testB.push(b);
      }
      this.qA = this.testA[0];
      this.qB = this.testB[0];
    },
    makeMinus() {
      let sa = (this.selDigit === '한자리' ? 3 : (this.selDigit === '두자리' ? 30 : 300 ));
      let ea = (this.selDigit === '한자리' ? 9 : (this.selDigit === '두자리' ? 99 : 999 ));
      let sb = (this.selDigit === '한자리' ? 1 : (this.selDigit === '두자리' ? 10 : 100 ));
      let eb = (this.selDigit === '한자리' ? 9 : (this.selDigit === '두자리' ? 49 : 490 ));
      let cb =  (this.selDigit === '한자리' ? 1 : (this.selDigit === '두자리' ? 10 : 100 ));

      for ( let i = 0; i < this.examCount; i++) {
        let a = Math.floor((Math.random() * (ea - sa)) + sa);
        let b = Math.floor((Math.random() * (eb - sb)) + sb);
        console.log(a, b);

        /* eslint-disable */
        while (true) {
          if (a < b) {
            console.log(a, b);
            a = Math.floor((Math.random() * (ea - sa)) + sa);
          } else if (a - b < 0) {
            a = Math.floor((Math.random() * (ea - sa)) + sa);
            b = Math.floor((Math.random() * (cb - sb)) + sb);
            console.log(a, b);
          } else {
            break;
          }
        }

        this.testA.push(a);
        this.testB.push(b);
      }
      this.qA = this.testA[0];
      this.qB = this.testB[0];
    },
    shuffle(array) {
      array.sort(() => Math.random() - 0.5);
    },
    showResult(){
      this.answers[this.page] = this.answer;

      if (this.selSymbol === '÷') {
        this.answers1[this.page] = this.answer1;
      }

      if (this.selSymbol === '구구단' || this.selSymbol === 'X') {
        this.scoring[this.page] = (this.qA * this.qB === this.answer) ? 1 : 0;
      } else if (this.selSymbol === '÷') {
        this.scoring[this.page] = (parseInt(this.qA / this.qB) === this.answer) ? ((this.qA % this.qB === this.answer1) ? 1 : 0) : 0;
      } else if (this.selSymbol === '+') {
        this.scoring[this.page] = (this.qA + this.qB === this.answer) ? 1 : 0;
      } else if (this.selSymbol === '-') {
        this.scoring[this.page] = (this.qA - this.qB === this.answer) ? 1 : 0;
      }

      let oScore = 0;
      let xScore = this.scoring.length;

      for (let i = 0; i < this.scoring.length; i++) {
        if (this.scoring[i] < 0) {
          alert('문제 풀이를 완료하지 않은 문항이 있습니다.');
          return;
        }

        oScore += this.scoring[i];
        xScore -= this.scoring[i];
      }

      this.$emit('checkCorrect',(oScore === this.scoring.length));
      this.$emit('setStartDate', this.startDate);

      let symbolStr = '없음';
      let digitStr = '없음';

      if (this.selSymbol === '+') {
        symbolStr = '더하기';
        digitStr = this.selDigit;
      } else if (this.selSymbol === '-') {
        symbolStr = '빼기';
        digitStr = this.selDigit;
      } else if (this.selSymbol === 'X') {
        symbolStr = '곱하기';
        digitStr = this.selDigit;
      } else if (this.selSymbol === '구구단') {
        symbolStr = '구구단';
        digitStr = this.selDigit + '단';
      } else if (this.selSymbol === '÷') {
        symbolStr = '나누기';
        digitStr = this.selDigit;
      }

      let scoreStr = symbolStr + '(' + digitStr + ') 완료 : 전체 {A}건 정답 {O}건 오답 {X}건';

      console.log(this.scoring);

      scoreStr = scoreStr.replace('{A}', this.scoring.length).replace('{O}', oScore).replace('{X}', xScore);
      this.$emit('scoreStr', scoreStr);
      // this.$emit('scoreStr', scoreStr);

      this.$parent.showResult(this.scoring, true);
    }
  }
}
</script>

