<template>
  <h2 class="timer-main">{{ TimerStr }}</h2>
</template>

<script>
export default {
  name: "Timer",
  emits: ['isTimeover'],
  data() {
    return {
      Timer: null,
      TimerStr: "",
      TimeCounter: 5,
    }
  },
  props: {
    testTime: Number
  },
  methods: {
    // startTimer() {
    //   let time = 600;
    //   let min = '';
    //   let sec = '';
    //
    //   let x = setInterval(function() {
    //     min = parseInt(time / 60);
    //     sec = time % 60;
    //     this.currTime = min + '분' + sec + '초';
    //     time--;
    //
    //     console.log(this.currTime);
    //
    //     if (time < 0) {
    //       clearInterval(x);
    //       alert('시간 초과로 종료 됩니다.\n문제를 처음부터 시작하세요.');
    //       this.$emit('isTimeover', true);
    //     }
    //   }, 1000);
    // },
    timerStart: function() {
      // 1초에 한번씩 start 호출
      console.log(this.testTime);
      this.TimeCounter = this.testTime;
      var interval = setInterval(() => {
        this.TimeCounter--; //1초씩 감소
        this.TimerStr = this.prettyTime();
        if (this.TimeCounter <= 0) this.timerStop(interval);
      }, 1000);
      return interval;
    },
    timerStop: function(Timer) {
      clearInterval(Timer);
      this.TimeCounter = 0;
      alert('시간 초과로 종료 됩니다.\n문제를 처음부터 시작하세요.');
      this.$emit('isTimeover', true);
    },
    prettyTime: function() {
      // 시간 형식으로 변환 리턴
      let time = this.TimeCounter / 60;
      let minutes = parseInt(time);
      let secondes = Math.round((time - minutes) * 60);
      return (
          "남은시간 : " +
          minutes.toString().padStart(2, "0") +
          "분" +
          secondes.toString().padStart(2, "0") +
          "초"
      );
    },
  },
  mounted() {
    //문자발송성공시 호출
    if(this.Timer != null){
      this.timerStop(this.Timer);
      this.Timer = null;
    }
    this.Timer = this.timerStart();
  },
}
</script>

