<template>
    <div v-if="isLogin">
      <h2 class="user-header">{{ user_name }}</h2>
    </div>
    <div class="login-wrapper" v-else-if="isLogin === false">
      <div class="input-group mb-3">
        <input v-model="mb_id" type="text" class="form-control" placeholder="User ID" aria-label="User ID" aria-describedby="button-addon2">
        <input v-model="mb_pw" type="password" class="form-control" placeholder="User Password" aria-label="User Password" aria-describedby="button-addon2">
        <button @click="doLogin" class="btn btn-outline-secondary" type="button" id="button-addon2">LOGIN</button>
      </div>
    </div>
</template>

<script>
export default {
  name: "UserInfo",
  emits: ['mb_name_change', 'mb-id', 'isLogin'],
  data() {
    return {
      // isLogin: false,
      mb_id: '',
      mb_pw: '',
      user_name: '',
      currTime: '',
    }
  },
  methods: {
    doLogin() {
      let loginFail = true;

      for (var i = 0; i < this.userData.length; i++) {
        if (this.userData[i].mb_id === this.mb_id) {
          if (this.userData[i].mb_pw === this.mb_pw) {
            this.$emit('mb_name_change', this.userData[i].mb_name);
            this.$emit('mb-id', this.userData[i].mb_id);
            this.$emit('set-chat-id', this.userData[i].telegramIds);
            this.$emit('isLogin', true);
            // this.emitter.emit('isLogin', true);

            this.user_name = this.userData[i].mb_name;
            // this.isLogin = true;
            this.telegramIds = this.userData[i].telegramIds;
            console.log('로그인 성공!!');

            loginFail = false;
            break;
          }
        }
      }

      if (loginFail) {
        alert('아이디 또는 비밀번호가 잘 못 되었습니다.');
      }
    },
  },
  props: {
    mb_name: String,
    userData: Array,
    isLogin: Boolean,
  }
}
</script>

<style>
.login-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
}
</style>
