<template>
  <div class="container text-center" style="overflow: auto; width: 100%; height: 400px;">
    <div class="row">
      <div class="col" style="border: 1px solid #000;">문항</div>
      <div class="col" style="border: 1px solid #000;">성적</div>
    </div>
    <div class="row" v-for="(score, i) in scoring" :key="i">
      <div class="col" style="border: 1px solid #000">{{ i + 1 }}</div>
      <div class="col" style="border: 1px solid #000; color: blue; " v-if="score === 1">O</div>
      <div class="col" style="border: 1px solid #000; color: red; " v-else>X</div>
    </div>
    <div class="row">
      <button class="btn btn-outline-primary" @click="callSubmit">제출</button>
    </div>
  </div>
</template>

<script>
export default {
  name: "ResultTable",
  props: {
    scoring: Array
  },
  methods: {
    callSubmit() {
      this.$parent.doSubmit();
    }
  }
}
</script>

